import React, { lazy, Suspense, useContext } from "react";
import { DataContext } from "../../App";
import "./homelist.css";

const HomeCard = lazy(() => import("../homecard/HomeCard"));
const MAX_ELEMENTS = 6;

const HomeList = React.memo(() => {
  const { data } = useContext(DataContext);
  const filteredData = data.slice(0, MAX_ELEMENTS);

  return (
    filteredData.length > 0 && (
      <div className="homelist-ctn">
        <h3>
          <small>Nos logements de rêves parlent d'eux même !</small>
          <span>Populaires</span>
        </h3>
        <div className="cards-grid">
          {filteredData.map((item, index) => (
            <Suspense key={index} fallback={<span>Loading...</span>}>
              <HomeCard
                id={item.id}
                title={item.title}
                zipCode={item.zipCode}
                city={item.city}
                level={item.level}
                rooms={item.rooms}
                photos={item.photos.slice(0, 5)}
                price={item.price}
                livingArea={item.livingArea}
                salerEmail={item.professional.email}
                publicationId={item.publicationId}
              />
            </Suspense>
          ))}
        </div>
      </div>
    )
  );
});

export default HomeList;
